<script>
import accounting from 'accounting-js'
import { maskCurrencyBRL } from '@/utils/masks'
import { vias } from '@/graphql/Via.gql'

export default {
  props: {
    finalValue: {
      type: [String, Number],
      required: true
    },
    form: {
      type: Object,
      required: true
    },
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  apollo: {
    vias: {
      query: vias
    }
  },
  data () {
    return {
      vias: [],
      maskCurrencyBRL: maskCurrencyBRL({ numeralPositiveOnly: true })
    }
  },
  computed: {
    hasDownPayment () {
      return accounting.unformat(this.form.down_payment, ',') > 0
    }
  }
}
</script>

<template>
  <div class="mt-3">
    <div class="row">
      <div class="col-12 col-sm">
        <AppInput
          :value="finalValue"
          disabled
          name="final_value"
          disabled-message="O preo final  calculado automaticamente."
        >
          Preo final
        </AppInput>
      </div>
      <div
        class="col-12 col-sm"
      >
        <AppInput
          id="discount"
          :value="form.discount"
          :mask="maskCurrencyBRL"
          name="discount"
          :error="form.errors.get('discount')"
          optional
          numeric
          @input="form.set({
            discount: $event
          })"
        >
          Desconto
        </AppInput>
      </div>
    </div>

    <div
      v-if="!isEdit"
      class="row"
    >
      <div class="col-12 col-sm">
        <AppInput
          id="down_payment"
          :value="form.down_payment"
          name="down_payment"
          :mask="maskCurrencyBRL"
          :error="form.errors.get('down_payment')"
          optional
          @input="form.set({
            down_payment: $event
          })"
        >
          Entrada
        </AppInput>
      </div>
      <div class="col-12 col-sm">
        <AppSimpleSelect
          :value="form.payment_via_id"
          name="payment_via_id"
          :options="vias"
          label-prop="name"
          :error="form.errors.get('payment_via_id')"
          :disabled="!hasDownPayment"
          @input="form.set({
            payment_via_id: $event
          })"
        >
          Via da entrada
        </AppSimpleSelect>
      </div>
    </div>
  </div>
</template>
